  outline: none;
  padding: 0;
  text-align: center;
  user-select: none;
}

button:hover,
.btn:hover {
  text-decoration: none;
}

button:focus, button:active,
.btn:focus,
.btn:active {
  box-shadow: none;
  outline: none;
  text-decoration: none;
}

.clearfix {
  zoom: 1;
}

.clearfix::before, .clearfix::after {
  content: '';
  display: table;
}

.clearfix::after {
  clear: both;
}

.flag {
  display: table;
  width: 100%;
}

.flag > .left,
.flag > .right,
.flag > .body {
  display: table-cell;
  vertical-align: middle;
}

.flag > .left img,
.flag > .right img,
.flag > .body img {
  display: block;
  max-width: none;
}

.flag > .body {
  width: 100%;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

input {
  border: none;
  outline: none;
  background-color: transparent;
}

html {
  font-family: Helvetica, Arial, sans-serif, Helvetica, Arial, sans-serif;
  background-color: #fafafa;
  color: #111;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

address {
  font-style: normal;
}

/**
 * GRID
 *
 * Column sizes are determined by the parent class in percentages. i.e .grid.grid-50 results in 50% width columns.
 * We use inline-block for this column system. Any child columns have their font size reset to $body-font-size
 * `.with-breakpoints` adds responsive: 33% / 25% turns into 100% on tablet, and 50% turns into 100% on mobile.
 */
.grid-layout {
  font-size: 0;
}

.grid-layout > .grid-item {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
}

.grid-layout.cols-50 > .grid-item {
  width: 50%;
}

@media all and (max-width: 767px) {
  .grid-layout.cols-50.with-breakpoints > .grid-item {
    width: 100%;
  }
}

.grid-layout.cols-33 > .grid-item {
  width: 33.33333%;
}

@media all and (max-width: 991px) {
  .grid-layout.cols-33.with-breakpoints > .grid-item {
    width: 100%;
  }
}

.grid-layout.cols-25 > .grid-item {
  width: 25%;
}

@media all and (max-width: 991px) {
  .grid-layout.cols-25.with-breakpoints > .grid-item {
    width: 100%;
  }
}

.grid-layout img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Georgia, "Times New Roman", serif, Helvetica, Arial, sans-serif;
}

.maxwidth-wrapper {
  max-width: 1240px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  position: relative;
}

.media {
  margin: 0;
  overflow: visible;
  zoom: 1;
}

.media::before, .media::after {
  content: '';
  display: table;
}

.media::after {
  clear: both;
}

.media > .left img,
.media > .right img {
  display: block;
}

.media > .left {
  float: left;
}

.media > .right {
  float: right;
}

.media > .body {
  overflow: hidden;
  _overflow: visible;
  zoom: 1;
}

.view-ui {
  max-width: 800px;
  margin: 0 auto;
}

.ui-container {
  padding: 25px;
  background-color: #f2f2f2;
  color: #111;
}

.ui-container.ui-reversed {
  background-color: #090909;
  color: #fafafa;
}

.ui-container.ui-inline-children > * {
  display: inline-block;
  vertical-align: middle;
  margin: 5px 15px;
}

.ui-container.ui-icons {
  background-color: #6b7a94;
}

.ui-container.ui-icons img {
  margin: 5px;
  max-width: 40%;
  max-height: 205px;
  vertical-align: middle;
}

.ui-title {
  margin: 0;
  padding: 15px;
  background-color: #eee;
  font-size: 2em;
}

.ui-header {
  font-size: 3em;
  text-align: center;
  margin: 0;
  padding: 20px;
}

.ui-center {
  text-align: center;
}

img.alignleft,
img.alignright {
  display: block;
  margin: 0.5em auto;
  max-width: 100%;
  height: auto;
}

@media all and (min-width: 768px) {
  .alignright,
  img.alignright {
    margin-left: 1.5em;
    float: right;
  }
  .alignleft,
  img.alignleft {
    float: left;
    margin-right: 1.5em;
  }
}

.aligncenter,
img.aligncenter {
  margin-right: auto;
  margin-left: auto;
  display: block;
  clear: both;
}

.wp-caption {
  margin-bottom: 1.5em;
  text-align: center;
  padding-top: 5px;
}

.wp-caption img {
  border: 0 none;
  padding: 0;
  margin: 0;
}

.wp-caption p.wp-caption-text {
  line-height: 1.5;
  font-size: 10px;
  margin: 0;
}

.wp-smiley {
  margin: 0 !important;
  max-height: 1em;
}

blockquote.left {
  margin-right: 20px;
  text-align: right;
  margin-left: 0;
  width: 33%;
  float: left;
}

blockquote.right {
  margin-left: 20px;
  text-align: left;
  margin-right: 0;
  width: 33%;
  float: right;
}

/**
 * MODIFIERS
 *
 * These are utility classes that you can attach to other elements to alter their behaviour. They shouldn't have any
 * big styles on their own, only provide some behaviour that is frequently used in the app. For example:
 * floats, grids, mixins
 */
.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

/**
 * COMPONENTS
 *
 * These should make up most of the UI for the website. Every self contained part of the website should be its own component.
 * Try following these rules to make sure that things don't get out of hand:
 *
