 *    Instead, you should apply modifiers inside of its own component (for example, .element.dark { background-color: black })
 */
.input-field-text {
  font-family: Helvetica, Arial, sans-serif, Helvetica, Arial, sans-serif;
  height: 60px;
  line-height: 60px;
  display: block;
  position: relative;
}

.input-field-text.has-error .error-msg {
  display: inline-block;
}

.input-field-text .error-msg {
  text-transform: lowercase;
  font-size: 0.8em;
  display: none;
}

.input-field-text .error-msg::before {
  content: '(';
}

.input-field-text .error-msg::after {
  content: ')';
}

.input-field-text input:focus,
.input-field-text input.has-text,
.input-field-text textarea:focus,
.input-field-text textarea.has-text {
  padding-top: 13px;
}

.input-field-text input:focus ~ .label-mid,
.input-field-text input.has-text ~ .label-mid,
.input-field-text textarea:focus ~ .label-mid,
.input-field-text textarea.has-text ~ .label-mid {
  color: black;
  transform: translateY(-15px);
  font-size: 14px;
}

.input-field-text input,
.input-field-text select,
.input-field-text textarea {
  border: 1px solid rgba(208, 204, 198, 0.2);
  transition: border-color 150ms;
  width: 100%;
}

.input-field-text input:hover, .input-field-text input:focus,
.input-field-text select:hover,
.input-field-text select:focus,
.input-field-text textarea:hover,
.input-field-text textarea:focus {
  border-color: rgba(208, 204, 198, 0.3);
}

.input-field-text input,
.input-field-text select {
  padding: 0 20px;
  height: 60px;
  border-radius: 3px;
}

.input-field-text.has-error input {
  border-color: #b6391c;
}

.input-field-text.has-error .label.label-mid {
  color: #b6391c;
}

.input-field-text .label {
  line-height: normal;
  position: absolute;
  left: 20px;
  transition: opacity 400ms, transform 400ms, visibility 400ms, color 400ms, font-size 400ms;
  cursor: text;
  user-select: none;
}

.input-field-text .label-mid {
  top: 20px;
  transform: translateY(0);
}

.input-group {
  margin-top: 10px;
}

